import React, { Component } from "react";
import { Grid, Cell } from "react-mdl";
import logo from "../Components/logo.png";
import Particles from "react-particles-js";

const particleOpt = {
  particles: {
    line_linked: {
      value: 80,
      density: {
        enable: true,
        value_area: 800
      }
    },
    shape: {
      type: "circle",
      stroke: {
        width: 0,
        color: "#000000"
      }
    },
    polygon: {
      nb_sides: 5
    },
    opacity: {
      value: 0.5,
      random: false,
      anim: {
        enable: false,
        speed: 1,
        opacity_min: 0.1,
        sync: false
      }
    },
    modes: {
      grab: {
        distance: 400,
        line_linked: {
          opacity: 1
        }
      }
    },
    size: {
      value: 3,
      random: true,
      anim: {
        enable: false,
        speed: 40,
        size_min: 0.1,
        sync: false
      }
    },
    move: {
      enable: true,
      speed: 6,
      direction: "none",
      random: false,
      straight: false,
      out_mode: "out",
      bounce: false,
      attract: {
        enable: false,
        rotateX: 600,
        rotateY: 1200
      }
    }
  },
  interactivity: {
    detect_on: "canvas",
    events: {
      onhover: {
        enable: false,
        mode: "repulse"
      },
      onclick: {
        enable: true,
        mode: "push"
      },
      resize: true
    },
    modes: {
      grab: {
        distance: 800,
        line_linked: {
          opacity: 1
        }
      },
      bubble: {
        distance: 400,
        size: 40,
        duration: 2,
        opacity: 8,
        speed: 3
      },
      push: {
        particles_nb: 4
      },
      remove: {
        particles_nb: 2
      }
    }
  },
  repulse: {
    distance: 400,
    duration: 1
  },
  retina_detect: true
};
class Landing extends Component {
  render() {
    return (
      <div style={{ width: "auto", margin: "auto" }}>
        <Grid className="landing-grid">
          <Particles className="particles" params={particleOpt} />
          <Cell col={12}>
            <img className="avatar-img" src={logo} alt="logo" />
            <div className="banner-text">
              <h1>FULL STACK WEB DEVELOPER</h1>
              <hr />
              <p>HTML5 / CSS3 | BOOTSTRAP | REACT JS | WORDPRESS</p>
              <div className="social-links">
                {/*Linkdin */}
                <a
                  href="https://www.linkedin.com/in/hicham-fatih-6ba462151/"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <i class="fa fa-linkedin-square" aria-hidden="true" />
                </a>
                {/*Github */}
                <a
                  href="https://github.com/FatihHicham"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <i class="fa fa-github-square" aria-hidden="true" />
                </a>
                {/*Gitlab */}
                <a
                  href="https://gitlab.com/Hixam"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <i class="fa fa-gitlab" aria-hidden="true" />
                </a>
                {/*Youtube */}
                <a
                  href="https://www.youtube.com/channel/UCVKSQovJTQXrtKIIFhpeKNQ?view_as=subscriber"
                  rel="noopener noreferrer"
                  target="_blank"
                >
                  <i class="fa fa-youtube-square" aria-hidden="true" />
                </a>
              </div>
            </div>
          </Cell>
        </Grid>
      </div>
    );
  }
}
export default Landing;
